import React from 'react'
import { useEffect, useState } from 'react'
import axios from 'axios';
import { useNavigate } from "react-router-dom";
import "../styles/Curator.css"
import "../styles/Form.css"
import { useAuth } from "../contexts/AuthContexts"
const Rooms = (props) => {
  const refresh = () => window.location.reload(true)
  const { role } = useAuth()
  const { currentUser } = useAuth()
  const [loading, setLoading] = useState(false)
  const [response, setResponse] = useState([])
  const [currentRoom, setCurrentRoom] = useState(null)

  let navigate = useNavigate();
  const routeChange = (path) => {
    navigate(path);
  }
  const verify = (room_number, user_id) => {
    axios.post('http://localhost:5000/verify', { room_n: room_number, u_id: user_id})
      .then(res => {
        let new_rooms = []
        for (let room of response) {
          let new_room = room
          if (room.number === room_number) {
            new_room.verified = true
          }
          new_rooms.append(new_room)
        }
        setResponse(new_rooms)
      }).catch(err =>
        setLoading(false))
  

 
}
useEffect(() => {
  setLoading(true)
  axios.get(`http://localhost:5000/curator_rooms/${props.user_id}`)
    .then(res => {
      console.log(res)
      setResponse(res.data)
      setLoading(false)
    }).catch(err =>
      setLoading(false))
}, []);
return (
  <div className="main"
    style={{ minHeight: '100vh' }}>
    {currentRoom === null ? <>
      {loading ? <>
        <text styles={{}}>...</text>

      </> : <><div className="header">
        <div className="logo">RooMy</div>
        <div className="button_group">
          <button onClick={() => routeChange("/")} className="header_button-home"></button>
          {role == "ADMIN" ? <>
                <button onClick={() => routeChange("/manager")} className="header_button-login"></button>
          </> : <></>}
        </div>
      </div>
        <div className="main-text">
          <strong> </strong>
        </div>
        <div className="rooms">
          {response.map((ele, index) => (
            <div className="header_button-room" onClick={() => setCurrentRoom(ele)}>{ele.number}{ele.verified ? "" : ""}</div>
          ))}
        </div></>}
    </> : <>
      <div className='main'>
        <div className='info'><strong>   {currentRoom.number}</strong></div>
        <div className='name'>
          <strong>' :</strong>
          {currentRoom.names.map((name, index) => (
            <div key={name + index}>{name}</div>
          ))}
        </div>
        <div className='name'>
          {currentRoom.furniture_list.map((list_fur, index_fur) => (
            <>
              {list_fur.map((furniture, index) => (
                <div key={"furniture_div" + index + index_fur}>
                  <div><strong>{furniture.type_expanded}</strong></div>
                  :
                  <div>{furniture.description}</div>
                  {furniture.owner ? <>
                    <div>{furniture.owner + "owner"}</div>
                  </> : <></>}
                  <>{furniture.images!==null? <img className='image' src={furniture.images}/> : <label> </label>}</>
                </div>
              ))}
            </>
          ))}

        </div>
        <div className='verify'>
        <button className='verify_button' onClick={() => verify(currentRoom.number, currentUser._delegate.uid)}></button>
        <button className='verify_button' onClick={refresh}></button>
        </div>
      </div>
    </>}

  </div>
)
}

export default Rooms